  .bar {
    position: fixed;
    animation: animateBar 1s linear infinite;
    background-image: linear-gradient(0deg,#5cc1cf,#c14aae,#5cc1cf);
    background-size: 100% 1100%;
    height: 8vh;
    width: 100%;
    z-index: 40;
  }
  
  &::after {
    position: absolute;
    content: "";
    z-index: -1;
    width: calc(100% + var(--border-width) * 2);
    height: calc(100% + var(--border-width) * 2);
    background: linear-gradient( 60deg, hsl(224, 85%, 66%), hsl(269, 85%, 66%), hsl(314, 85%, 66%), hsl(359, 85%, 66%), hsl(44, 85%, 66%), hsl(89, 85%, 66%), hsl(134, 85%, 66%), hsl(179, 85%, 66%) );
    background-size: 300% 300%;
    background-position: 0 50%;
    animation: banimateBar 1s alternate infinite;
  }
  
