  .video-background-holder {
    position: relative;
    background-color: black;
    height: calc(100vh);
    min-height: 25rem;
    width: 100%;
    overflow: hidden;
    pointer-events:auto;
  }
  
  .video-background-holder-b {
    position: relative;
    background-color: black;
    height: calc(100vh  );
    min-height: 25rem;
    width: 100%;
    overflow: hidden;
    pointer-events:auto;
  }
  
  .video-background-holder video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: 0;
    -ms-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    pointer-events:auto;
  }
  
  .video-background-holder-b video {
    pointer-events:auto;
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 90%;
    width: auto;
    height: auto;
    z-index: 0;
    -ms-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
  }
  
  .video-background-content {
    position: relative;
    z-index: 2;
    pointer-events:auto;
  }
  
  .video-background-overlay {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.75;
    z-index: 1;
    pointer-events:auto;
  }
  
  .video-background-overlay-b {
    position: absolute;
    top: 0;
    left: 0;
    height: 90%;
    width: 100%;
    background-color: black;
    opacity: 0.5;
    z-index: 1;
    pointer-events:auto;
  }

  










